* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
  font-family: Rubik Distressed, cursive;
}

a {
  position: fixed;
  top: 15px;
  left: 20px;
}

button[data-start] {
  color: #fff;
  width: 500px;
  height: 148px;
  cursor: pointer;
  background: #d6f106;
  border: none;
  border-radius: 10px;
  margin: 50px;
  font-size: 72px;
  position: relative;
}

button[data-stop] {
  color: #fff;
  width: 500px;
  height: 148px;
  cursor: pointer;
  background: #1e0f53;
  border: none;
  border-radius: 10px;
  margin: 50px;
  font-size: 72px;
  position: relative;
}

